import React, {useRef, useEffect, useState} from 'react';
import {select, geoPath, geoMercator, min, max, scaleLinear} from 'd3';
import useResizeObserver from './useResizeObserver';

function WorldMap({data, property}) {
    const svgRef = useRef();
    const wrapperRef = useRef();
    const dimensions = useResizeObserver(wrapperRef);
    const [selectedCountry, setSelectedCountry] = useState(null);

    // will be called initially and on every data change
    useEffect(() => {
        const svg = select(svgRef.current);

        const minProp = min(data.features, (feature) => feature.properties[property]);
        const maxProp = max(data.features, (feature) => feature.properties[property]);
        const colorScale = scaleLinear().domain([minProp, maxProp]).range(['#C4B5FD', '#4C1D95']);

        // use resized dimensions
        // but fall back to getBoundingClientRect, if no dimensions yet.
        const {width, height} = dimensions || wrapperRef.current.getBoundingClientRect();

        console.log(width)
        console.log(height)
        console.log(dimensions)

        // projects geo-coordinates on a 2D plane
        const projection = geoMercator()
            .fitSize([width, height], selectedCountry || data)
            .precision(100);

        // takes geojson data,
        // transforms that into the d attribute of a path element
        const pathGenerator = geoPath().projection(projection);

        // render each country
        svg.selectAll('.country')
            .data(data.features)
            .join('path')
            .on('click', (feature) => {
                console.log(feature)
                console.log(this)
                console.log(data)
                console.log(selectedCountry)

                // setSelectedCountry(selectedCountry === feature ? null : feature);
            })
            .attr('class', 'country')
            .transition()
            .attr('fill', (feature) => colorScale(feature.properties[property]))
            .attr('d', (feature) => pathGenerator(feature));

        // render text
        // svg.selectAll('.label')
        //     .data([selectedCountry])
        //     .join('text')
        //     .attr('class', 'label')
        //     .text(
        //         (feature) => feature && feature.properties.name + ': ' + feature.properties[property].toLocaleString()
        //     )
        //     .attr('x', 10)
        //     .attr('y', 25);
    }, [data, dimensions, property, selectedCountry]);

    return (
        <div className="relative w-full" ref={wrapperRef} style={{marginBottom: '2rem'}}>
            <svg ref={svgRef}></svg>
        </div>
    );
}

export default WorldMap;
